export function HandleBooleanAttribute(name, newValue) {
    // Si l'attribut n'existe pas du tout, retourne false
    if (!this.hasAttribute(name)) {
        return false;
    }
    // Si l'attribut existe mais n'a pas de valeur (null), considre cela comme true
    if (newValue === null) {
        return true;
    }
    console.log("undefined");
    // Si la nouvelle valeur est prsente, on vrifie qu'elle n'est pas "false"
    return newValue.toLowerCase() !== "false";
}
