import { PfWebComponent } from "../models/PfWebComponent";
export function cretateOnSlotChangeHandler(slotName = null, callback) {
    return (target) => {
        let childrens = target.childNodes;
        let [children] = [...target.childNodes].map((element) => {
            if (element instanceof PfWebComponent == false)
                return null;
            /// ## Aucun slot
            if (!slotName && element?.getAttribute('slot') == null)
                return element;
            /// ## Avec slot + correspondance
            else if (slotName && element?.getAttribute('slot') == slotName)
                return element;
            /// ### Sinon return null
            else
                return null;
        }).filter(x => x);
        if (callback)
            callback(children);
    };
}
